@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600&display=swap");
html {
  font-size: 0.9375rem;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins";
  font-weight: 400;
  text-align: center;
  background-color: #fafafa;
  color: #4c4e61;
  max-width: 72.5rem;
  padding-block: 4rem;
  padding-inline: 1.5rem;
}

.footer__attribution {
  font-size: 1.075rem;
  text-align: center;
}
.footer__attribution a {
  color: #3e52a3;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.wrapper {
  margin: 0 auto;
}

.content {
  display: flex;
  flex-flow: column nowrap;
  gap: 1.5rem;
}
@media (min-width: 40em) {
  .content {
    flex: 1 1 auto;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: center;
    padding: 0;
  }
  .content > * {
    flex: 1 1 33%;
  }
}

.flex__content {
  display: flex;
  flex-flow: column nowrap;
  gap: 1.5rem;
}
@media (min-width: 40em) {
  .flex__content {
    flex-flow: column nowrap;
  }
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.content__card {
  padding: 1.85rem;
  border-radius: 10px;
  text-align: left;
  box-shadow: 0px 10px 20px 3px rgba(0, 0, 0, 0.1);
}
.content__card .card__heading {
  font-size: 1.45rem;
  font-weight: 600;
}
.content__card .card__desc {
  font-weight: 400;
  color: #a3a5ae;
  line-height: 1.5;
  margin: 0.75rem 0;
}
.content__card .card__icon {
  width: 64px;
  margin-top: 2rem;
  margin-left: auto;
}

.content .c__supervisor {
  border-top: 4px solid #45d3d3;
}
.content .flex__content .c__team-builder {
  border-top: 4px solid #ea5353;
}
.content .flex__content .c__karma {
  border-top: 4px solid #fcaf4a;
}
.content .c__calc {
  border-top: 4px solid #549ef2;
}

.header {
  text-align: center;
}
.header .header__title {
  font-size: 1.5rem;
  font-weight: 200;
}
.header .header__subtitle {
  font-size: 1.5rem;
  font-weight: 600;
  margin-top: 0.75rem;
}
.header .header__desc {
  font-size: 0.95rem;
  font-weight: 400;
  color: #a3a5ae;
  margin: 1.375rem 0;
}

.content {
  padding: 0;
  margin-top: 5rem;
}
.content .flex__content {
  padding: 0;
  margin: 0 auto;
}

@media (min-width: 40em) {
  .header {
    margin: 0 auto;
    max-inline-size: 500px;
  }
  .header .header__title, .header .header__subtitle {
    font-size: 2.35rem;
  }
  .header .header__desc {
    font-size: 1.075rem;
  }

  .content {
    margin-bottom: 5rem;
  }
}
@media (max-width: 39.9375em) {
  .footer__attribution {
    display: none;
  }
}